export default function TechStack() {
  return (
    <div className="flex justify-center gap-6 mb-10 flex-wrap">
      {techLogos.map((tech, index) => (
        <div key={tech.name} className="group relative" style={{ animationDelay: `${index * 100}ms` }}>
          <div className="w-16 h-16 bg-slate-800/50 backdrop-blur-sm rounded-xl border border-slate-700 flex items-center justify-center transition-all duration-300 hover:scale-110 hover:border-cyan-400/50 hover:shadow-lg hover:shadow-cyan-500/25">
            <svg viewBox="0 0 24 24" className="w-10 h-10" fill={tech.color}>
              <path d={tech.svg} />
            </svg>
          </div>
          <span className="absolute -bottom-8 left-1/2 -translate-x-1/2 text-xs text-gray-400 opacity-0 group-hover:opacity-100 transition-opacity whitespace-nowrap">
            {tech.name}
          </span>
        </div>
      ))}
    </div>
  );
}